    // advisers
    'teamAdvisersPartners#59': 'Keith Teare',
    'teamAdvisersPartners#60': 'Gründer und Vorstandsvorsitzender von Accelerated Digital Ventures',
    'teamAdvisersPartners#61': 'Berater von ICOBox, Crypterium, EnergiMine etc.',
    'teamAdvisersPartners#62': 'Gründungsgesellschafter von TechCrunch',
    'teamAdvisersPartners#63': 'Ich bin Vorstandsvorsitzender und Vorsitzender des Investment-Kommittees bei Accelerated Digital Ventures, einer britischen Venture Company. Ich bin im Vereinigten Königreich geboren und aufgewachsen.',
    'teamAdvisersPartners#64': 'Ich bin Vorstandsvorsitzender und Vorsitzender des Investment-Kommittees bei Accelerated Digital Ventures, einer britischen Venture Company. Ich bin im Vereinigten Königreich geboren und aufgewachsen. Vorher war ich Gründer und Partner bei Archimedes Labs in Palo Alto, Kalifornien. Ich liebe es, mit Gründern, Teams, Designern und Ingenieuren zusammenzuarbeiten, um Produkte zu entwickeln, Geschichten zu erzählen und diese wahr werden zu lassen. Ich selbst bin seit den frühen 1980er-Jahren Gründer oder Mitbegründer vieler Unternehmen. Einige wurden erfolgreich verkauft, für andere gab es ein IPO\'d.',
    'teamAdvisersPartners#65': 'Zwei meiner Unternehmen waren sogenannte Einhörner, sie wurden also mit mehr als 1 Milliarde USD bewertet. Ich habe mich immer auf den Punkt konzentriert, an dem Veränderungen geschehen. In den 1980ern waren es Netzwerke und Datenbanken. In der Zeit von 1994 bis 1998 der Internetzugang. Von 1998 bis 2010 Webdienste und -inhalte sowie mobile Konsumenten-Applikationen; seit 2017 ist es die Blockchain.',
    'teamAdvisersPartners#66': 'Karriere und Projekte: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs und CINDX',
    'teamAdvisersPartners#67': 'Austin Kimm',
    'teamAdvisersPartners#68': 'CEO im Bereich internationaler Finanzdienstleistungen',
    'teamAdvisersPartners#69': '30+ Jahre Erfahrung im Bereich Finanzdienstleistungen',
    'teamAdvisersPartners#70': 'Gründete Unternehmen mit einem Wert von 500 Mio. USD',
    'teamAdvisersPartners#71': 'Finanzdienstleistungs-CEO mit erwiesener Erfahrung und besonderem Fokus auf den Aufbau von Versicherungsunternehmen in aufstrebenden Märkten, wo ich erfolgreich Unternehmen mit einer aktuellen Bewertung von über 500 Mio. USD aufgebaut habe.  Zuletzt in Russland (10 Jahre) sowie in Rollen außerhalb der Geschäftsleitung bei Versicherungen, darunter als CEO von Russlands größter Internetverkaufs-Beschäftigungsplattform, Workle. Hinzu kommt eine Reihe von C-Level-Positionen in Fintech-Start-ups wie Crypterium, der weltweit ersten digitalen Kryptobank.',
    'teamAdvisersPartners#72': 'Zuletzt in Russland (10 Jahre) sowie in Rollen außerhalb der Geschäftsleitung bei Versicherungen, darunter als CEO von Russlands größter Internetverkaufs-Beschäftigungsplattform, Workle. Hinzu kommt eine Reihe von C-Level-Positionen in Fintech-Start-ups wie Crypterium, der weltweit ersten digitalen Kryptobank.',
    'teamAdvisersPartners#73': 'Karriere und Projekte: Aviva, Commercial Union UK, Renaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex, CINDX',
    'teamAdvisersPartners#74': 'Philip Staehelin',
    'teamAdvisersPartners#75': '25 Jahre Erfahrung im Bereich Consulting und als Unternehmer',
    'teamAdvisersPartners#76': 'Unternehmen im Bereich alternative Zahlungsmethoden im Wert von 500 Mio. USD unter seiner Leitung',
    'teamAdvisersPartners#77': 'Mentor von 200+ Start-ups aus 20 Ländern',
    'teamAdvisersPartners#78': 'Ich bin ein Innovator und Leiter, der ansteckenden Optimismus mit pragmatischen Realismus vereint. Ich finde Möglichkeiten, wo andere keine sehen.',
    'teamAdvisersPartners#79': 'Ich habe meine Fähigkeiten, „die Dinge anders zu sehen“ und „Ideen in die Tat umzusetzen“, in unternehmerischen, beraterischen und auch in Start-up-Umfeldern nützen können – all das brachte mir breitgefächerte neue Perspektiven, die den Nährboden für neue transformative Ideen bieten.',
    'teamAdvisersPartners#80': 'Das Finden von Lösungen für schwierige Herausforderungen ist das, was mich tagtäglich antreibt.',
    'teamAdvisersPartners#81': 'Karriere und Projekte: Roland Berger, Dot Glasses, Transparency International, StartupYard, Bethereum, Gjirafa Inc, Accenture, CINDX',
    'teamAdvisersPartners#82': 'Amarpreet Singh',
