    'teamAdvisersPartners#86': 'Technologie-/Digital-Enthusiast und ein erfahrener Profi mit jahrelanger Expertise in der Technologiebranche – Operations, Consulting und Innovation.',
    'teamAdvisersPartners#87': 'Er ist zertifizierter Cloud-Spezialist (Microsoft und AWS). Bekannt für Vielseitigkeit und Flexibilität. Exzellente analytische, strategische und Führungsfähigkeiten sowie ein beeindruckend guter Teamplayer.',
    'teamAdvisersPartners#88': 'Fachgebiete: Technology Consulting, Operations, Cloud Computing, Fintech, Innovation, ERP, Machine-Learning, Account-Management, Partner-Development, Business-Development und -Strategie.',
    'teamAdvisersPartners#89': 'Amarpreet Singh verfügt über einen sehr diversifizierten Arbeits- und Ausbildungshintergrund.  Er besitzt drei Masterabschlüsse und hat in Indien, Singapur, Frankreich, China, Südamerika, Korea und Kanada gewohnt/gearbeitet/studiert.',
    'teamAdvisersPartners#90': 'Karriere und Projekte: Microsoft, die Weltbank, Airbus, TokenAsia Platform, GOeurika, CINDX',
    'teamAdvisersPartners#91': 'Tyler Sanford',
    'teamAdvisersPartners#92': '8 Jahre im digitalen Marketing bei Yelp und Zenefits',
    'teamAdvisersPartners#93': '12 ICOs und 225 Mio. USD generiert',
    'teamAdvisersPartners#94': 'Früher Nutzer der Blockchain-Technologie',
    'teamAdvisersPartners#95': 'Dadurch, dass ich als Athlet aufgewachsen bin, habe ich mir Fähigkeiten angeeignet, die ich bereits in meiner Karriere und im persönlichen Umfeld einsetzen konnte.',
    'teamAdvisersPartners#96': 'Ich hatte das Glück zu sehen, was geschieht, wenn man sich sehr für etwas engagiert, und ich habe zudem gelernt, wie man mit den Höhen und Tiefen umgehen muss, die mit allen professionellen Karrieren einhergehen.',
    'teamAdvisersPartners#97': 'Ich bin ein extrem intrinsisch motivierter Mensch und suche immer nach Wegen, mein Wissen und meine Talente in allen Aspekten des Geschäftslebens auszubauen.',
    'teamAdvisersPartners#98': 'Ich scheue keine Herausforderung und beziehe meine Motivation daher, dass ich Hindernisse bewältige, um meine Ziele zu erreichen.',
