    'teamAdvisersPartners#123': 'Ich scheue keine Herausforderung und beziehe meine Motivation daher, dass ich Hindernisse bewältige, um meine Ziele zu erreichen.',
    'teamAdvisersPartners#124': 'Karriere und Projekte: IPG Holdings, DataBlockChain, TraXion by Puma Technologies, ELYSIAN, Etherinc, FTEC Foundation, CINDX',
    'teamAdvisersPartners#125': 'Bogdan Fiedur',
    'teamAdvisersPartners#126': 'Blockchain- und Kryptowährungsexperte, Investor, Unternehmer',
    'teamAdvisersPartners#127': 'Mitbegründer von bitJob und Präsident von Blockchainexperts.io',
    'teamAdvisersPartners#128': 'ICO-Berater, ICOBench-Experte',
    'teamAdvisersPartners#129': 'Bogdan Fiedur ist ein Smart-Contract-Entwickler, Krypto-Investor, Unternehmer und Präsident von http://blockchainexperts.io. Er verfügt über mehr als 20 Jahre Erfahrung im Bereich IT im Aufbau von E-Commerce-Websites und war während der letzten drei Jahre in die Blockchain-Entwicklung involviert.',
    'teamAdvisersPartners#130': 'Er ist auch ein ICO-Berater, der bis dato mit 20 erfolgreichen ICOs zusammenarbeitet und stark darin eingebunden ist, Präsentationen und Workshops zu Blockchain und Smart-Contract-Programmierung in seinem Heimatort Winnipeg zu geben.',
    'teamAdvisersPartners#131': 'Bogdan fokussiert sich auf die Blockchain-Branche und bietet Beratung, Consulting-Dienstleistungen und Kontakt zu Blockchain-Experten. Kürzlich hat Bogdan „Bitjob“ gegründet, eine Freelancer-Blockchain-Plattform für Studenten, und ist als Teammitglied Teil mehrerer ICO-Projekte wie PayPie.com.',
    'teamAdvisersPartners#132': 'Karriere und Projekte: US Blockchain Association, Tuurnt, Trends Project, BiNeuro, Zichain, Graphen Tech, CyClean, Swachhcoin, CINDX',
    'teamAdvisersPartners#133': 'Graham Doggart',
    'teamAdvisersPartners#134': '20 Jahre in der Unternehmensentwicklung, Fintech-Marketing-Compliance',
    'teamAdvisersPartners#135': 'Führte Kampagnen im Wert von 100 Mio. USD aus',
    'teamAdvisersPartners#136': 'Berater für 10+ Blockchain-Projekte in diesem Jahr',
    'teamAdvisersPartners#137': 'Ich bin Vorsitzender eines Stealth-Cryptocurrency-Projekts und seit 2012 Mitbegründer und leitender Stratege bei Dynamic Abundance, einem Blockchain- und Fintech-Beratungsunternehmen.',
    'teamAdvisersPartners#138': 'Ich spezialisiere mich auf die Strukturierung und Aufmachung von Token-Offerings und habe international an verschiedenen Blockchain-, Kryptowährungs- und ICO-Projekten gearbeitet.',
    'teamAdvisersPartners#139': 'Mehr als 20 Jahre lang habe ich Tech-Management-Konsultationen, juristische und finanzielle Compliance-Beratung, Wachstumsstrategien, Investorbeziehungen sowie Design und Autorschaft von Materialien angeboten.',
    'teamAdvisersPartners#140': 'Karrieren und Projekte: Stealth Cryptocurrency Project, Dynamic Abundance, ELYSIAN, Fox Trading FX, Profede, Worltdopoly, CINDX',
    'teamAdvisersPartners#141': 'Sadie Hutton',
    'teamAdvisersPartners#142': '25 Jahre im leitenden Management und Management-Consulting',
    'teamAdvisersPartners#143': 'Multimillionen-Dollar-Fundraiser mit Kampagnen im Wert von 75 Mio. GBP',
    'teamAdvisersPartners#144': 'ICO-Berater bei 8 Projekten',
    'teamAdvisersPartners#145': 'Ich bin der Gründer von Dynamic Abundance, einem Blockchain-Beratungsunternehmen.',
    'teamAdvisersPartners#146': 'Ich bin ein Spezialist für ICO-Analyse, Projektmanagement, alle wichtigen Aspekte des ICO-Prozesses, und ich bevorzuge den „praktischen“ Ansatz. Ich verfüge über gute Fähigkeiten im Unternehmens-Management und -Wachstum und bin dadurch in der Lage, ein Projekt von der Idee bis zur Umsetzung zu begleiten. Ich bin von Natur aus ein Problemlöser und nehme mich jedes Projekts, in das ich involviert bin, persönlich an. Ich liebe es zu gewinnen!',
    'teamAdvisersPartners#147': 'Mit über 20 Jahren Management-Erfahrung habe ich ein starkes Netzwerk aufgebaut und biete aktive Beratung bezüglich Finanzen, Tech, Verkauf, Marketing sowie Qualitätssicherung für wichtige Blockchain-Projekte.',
    'teamAdvisersPartners#148': 'Karriere und Projekte: Dynamic Abundance, ELYSIAN, Worldopoly, Confidential ICO, Fox Trading FX, Profede, Market Digital, CINDX',
    'teamAdvisersPartners#149': 'Jason King',
    'teamAdvisersPartners#150': '20 Jahre Erfahrung in der Geschäftsführung',
    'teamAdvisersPartners#151': 'Führte Unternehmensoperationen in 30 Ländern',
    'teamAdvisersPartners#152': 'Verantwortung für über 500 Mio. USD an G&V',
    'teamAdvisersPartners#153': 'Fast 20 Jahre lang war Jason Kind Vorstandsmitglied in den Bereichen Technologie, Medien und Telekommunikation und in die Strukturierung und das Aushandeln von Geschäftsdeals, das Verhandeln von Gewinnen, den Ausbau von Märkten und die erfolgreiche Positionierung von Organisationen involviert.',
    'teamAdvisersPartners#154': 'Vieler dieser Herausforderungen haben mich in erfolgreiche Rollen in der Restrukturierung und im Turnaround geführt – führen Sie Ihre Branche an, indem Sie die richtigen Menschen und Teams aufbauen, um die profitabelsten Ziele zu erreichen.',
    'teamAdvisersPartners#155': 'In addition to his interests in private equity and startup companies, Jason has worked in various groups including Deutsche Telekom, Telenor and UPC Liberty Global and Veon.',
    'teamAdvisersPartners#156': 'Jason verfügt über nahezu grenzenlose Erfahrung in verschiedenen Bereichen, darunter im Business- und Operations-Management, in den Bereichen strategische Planung und Finanzen sowie in der Kryptowährungs- und Blockchain-Branche.',
    'teamAdvisersPartners#157': 'Motti Peer',
    'teamAdvisersPartners#158': 'Mitbegründer der preisgekrönten global agierenden PR-Agentur Blonde 2.0',
    'teamAdvisersPartners#159': 'Gründer mehrerer Unternehmen in den Bereichen Sicherheit und Finanzen',
    'teamAdvisersPartners#160': 'Motti ist eine der treibenden Kräfte im PR-Bereich für Blockchain- und Krypto-Start-ups und -VCs.',
    'teamAdvisersPartners#161': 'Motti ist Gründer mehrerer Unternehmen in den Bereichen Sicherheit und Finanzen. Er verfügt über mehrjährige Erfahrung als Geld- und Hedge-Fonds-Manager und besitzt einen MBA-Abschluss im Finanzwesen und Marketing von der Manchester University sowie einen BA in Business Management von der Fairleigh Dickinson University.',
    'teamAdvisersPartners#162': 'Er ist ein Co-CEO von Blonde 2.0, einer preisgekrönten globalen PR-Agentur, auch bekannt als einer der „Movers and Shakers“ der Startup Nation bei Forbes. Als Co-CEO von Blonde 2.0 ist er verantwortlich für die Public-Relations-Aspekte sowie für Strategie- und Krisenmanagement.',
    'teamAdvisersPartners#163': 'Mit einem Team von 35 professionellen Mitarbeitern ist Blonde 2.0 ist eine der treibenden Kräfte in der PR für Blockchain- und Crypto-Start-ups und -VCs.',
    'teamAdvisersPartners#164': 'Leonard Grayver',
    'teamAdvisersPartners#165': 'Partner bei Greenberg, Whitcombe & Takeuchi',
    'teamAdvisersPartners#166': 'Repräsentierte über 400 Start-ups',
    'teamAdvisersPartners#167': 'Generierte mehr als 18 Mio. USD für sein Start-up, als er Student an der juristischen Fakultät war.',
    'teamAdvisersPartners#168': 'Leonard Grayver wurde in der UdSSR geboren und zog mit seinen Eltern als Kind in die USA. Während seines Jura-Studiums gründete er ein Start-up und generierte erfolgreich mehr als 18 Mio. USD für die Finanzierung.',
    'teamAdvisersPartners#169': 'Sein Start-up hat nicht überlebt, aber seitdem hat Leonard mehr als 400 Start-ups geholfen.',
    'teamAdvisersPartners#170': 'Insgesamt hat Leonard mehr als 10 Jahre Erfahrung mit VC-Finanzierung, Fondsstrukturierung und Due Diligence (repräsentiert sowohl Fonds als auch LPs). Sein Rat an alle Unternehmer – folgen Sie nicht den Trends, folgen Sie Ihren Interessen.',

