    // advisers
    'teamAdvisersPartners#59': 'Keith Teare',
    'teamAdvisersPartners#60': 'Fundador y presidente ejecutivo de Accelerated Digital Ventures',
    'teamAdvisersPartners#61': 'Asesor para ICOBox, Crypterium, EnergiMine, etc.',
    'teamAdvisersPartners#62': 'Accionista fundador de TechCrunch',
    'teamAdvisersPartners#63': 'Soy el presidente ejecutivo y presidente del comité de inversiones de Accelerated Digital Ventures, una empresa del Reino Unido. Nací y me crié en el Reino Unido.',
    'teamAdvisersPartners#64': 'Anteriormente, fui fundador y socio de Archimedes Labs en Palo Alto (California). Me encanta trabajar con fundadores, equipos, diseñadores e ingenieros para crear productos, contar historias y hacerlas realidad.',
    'teamAdvisersPartners#65': 'He fundado y cofundado varias empresas desde principios de los 80. Algunas se vendieron con éxito y otras salieron al mercado en una OPV. Dos de mis empresas fueron unicornios, valoradas en más de mil millones de dólares.',
    'teamAdvisersPartners#66': 'Siempre me he centrado en las áreas de cambio. En la década de los 80, fueron las redes y las bases de datos. En la época de 1994 a 1998, fue el acceso a Internet. De 1998 a 2010 fueron los servicios y contenidos web, así como las aplicaciones móviles para el consumidor. Desde 2017, es la tecnología de cadenas de bloques. Carrera y proyectos: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs y CINDX.',
    'teamAdvisersPartners#67': 'Austin Kimm',
    'teamAdvisersPartners#68': 'Director de servicios financieros internacionales',
    'teamAdvisersPartners#69': 'Más 30 años de experiencia en servicios financieros',
    'teamAdvisersPartners#70': 'Creación de empresas valoradas en 500 000 000 $',
    'teamAdvisersPartners#71': 'Director general experto en servicios financieros de eficacia demostrada con especial interés en la creación de negocios de seguros en mercados en desarrollo, donde he creado empresas con éxito valoradas actualmente en más de 500 000 000 $.',
    'teamAdvisersPartners#72': 'Más recientemente, he trabajado en Rusia durante 10 años y también he ocupado altos cargos ajenos a los seguros, como el de presidente general de Workle (la mayor plataforma de empleos comerciales de Internet), y una amplia variedad de puestos de nivel C en empresas emergentes del sector tecnofinanciero como Crypterium, el primer criptobanco digital del mundo. Carrera y proyectos:',
    'teamAdvisersPartners#73': 'Aviva, Commercial Union UK, Reinaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex y CINDX.',
    'teamAdvisersPartners#74': 'Philip Staehelin',
    'teamAdvisersPartners#75': '25 años de experiencia en asesoría y emprendimiento',
    'teamAdvisersPartners#76': 'Negocios de pagos alternativos de 500 000 000 $ bajo supervisión',
    'teamAdvisersPartners#77': 'Mentor de más de 200 empresas emergentes de 20 países',
    'teamAdvisersPartners#78': 'Soy innovador y líder con una combinación de optimismo contagioso y realismo pragmático. Encuentro oportunidades invisibles para los demás.',
    'teamAdvisersPartners#79': 'He potenciado mi capacidad para "ver las cosas de otra forma" y "hacer las cosas realidad" en corporaciones, asesorías y empresas emergentes, lo que me ha aportado nuevas y amplias perspectivas que sirven de base para nuevas ideas transformadoras.',
    'teamAdvisersPartners#80': 'Buscar soluciones para grandes desafíos es lo que me saca de la cama cada mañana. ',
    'teamAdvisersPartners#81': 'Carrera y proyectos: Roland Berger, Dot Glasses, Transparency International, StartupYard, Bethereum, Gjirafa Inc, Accenture y CINDX.',
    'teamAdvisersPartners#82': 'Amarpreet Singh',
    'teamAdvisersPartners#83': 'Asesor experto de Global Blockchain Foundation',
    'teamAdvisersPartners#84': 'Experiencia en Microsoft, The World Bank y Airbus',
    'teamAdvisersPartners#85': 'Miembro del consejo asesor de proyectos de cadenas de bloques',
    'teamAdvisersPartners#86': 'Entusiasta de la tecnología digital y profesional avezado con años de experiencia en el sector tecnológico, concretamente en operaciones, asesoría e innovación.',
    'teamAdvisersPartners#87': 'Especialista en servicios en la nube certificado (Microsoft y AWS). Conocido por su versatilidad y flexibilidad. Goza de excelentes destrezas analíticas, estratégicas y de liderazgo además de ser un compañero de primera.',
    'teamAdvisersPartners#88': 'Especialidades: asesoría tecnológica, operaciones, informática en la nube, tecnofinanzas, innovación, ERP, aprendizaje automático, gestión de cuentas, desarrollo de socios, desarrollo empresarial y estrategia.',
    'teamAdvisersPartners#89': 'Amarpreet Singh cuenta con una experiencia laboral y una formación muy diversificadas. Tiene tres títulos de máster y ha vivido, trabajado y estudiado en India, Singapur, Francia, China, Sudáfrica, Corea y Canadá.',
    'teamAdvisersPartners#90': 'Carrera y proyectos: Microsoft, The World Bank, Airbus, TokenAsia Platform, GOeurika y CINDX.',
    'teamAdvisersPartners#91': 'Tyler Sanford',
    'teamAdvisersPartners#92': '8 años de marketing digital en Yelp y Zenefits',
    'teamAdvisersPartners#93': '12 ICO con una recaudación de más de 225 000 000 $',
    'teamAdvisersPartners#94': 'Uno de los primeros operadores en adoptar la tecnología de cadenas de bloques',
    'teamAdvisersPartners#95': 'Crecer como atleta me brindó unas destrezas que he podido aplicar a mi carrera profesional y vida personal.',
    'teamAdvisersPartners#96': 'He tenido la gran suerte de ver los resultados de ir un paso más lejos y, además, he aprendido a lidiar con los altibajos inherentes a toda profesión.',
    'teamAdvisersPartners#97': 'Soy una persona muy motivada, y siempre tengo ganas de seguir ampliando mis conocimientos y talentos en todos los aspectos de los negocios.',
    'teamAdvisersPartners#98': 'Siempre dispuesto a aceptar nuevos desafíos, mi motivación se basa en superar los obstáculos para alcanzar mis objetivos.',
    'teamAdvisersPartners#99': 'Carrera y proyectos: IPG Holdings, DataBlockChain, TraXion by Puma Technologies, ELYSIAN, Etherinc, FTEC Foundation y CINDX.',
