    'teamAdvisersPartners#125': 'Bogdan Fiedur',
    'teamAdvisersPartners#126': 'Experto, inversor y emprendedor en cadenas de bloques y criptomonedas',
    'teamAdvisersPartners#127': 'Cofundador de bitJob y presidente de Blockchainexperts.io',
    'teamAdvisersPartners#128': 'Asesor de ICO Advisor, experto de ICOBench',
    'teamAdvisersPartners#129': 'Bogdan Fiedur es desarrollador de contratos inteligentes, criptoinversor, empresario y presidente de http://blockchainexperts.io.',
    'teamAdvisersPartners#130': 'Cuenta con más de 20 años de experiencia en el sector de la informática, en la creación de sitios web de comercio electrónico, y lleva tres años trabajando en el desarrollo de las cadenas de bloques. También es asesor de ICO y ha trabajado con 20 ICO de éxito hasta la fecha.',
    'teamAdvisersPartners#131': 'Está muy involucrado en la organización de presentaciones y talleres sobre cadenas de bloques y programación de contratos inteligentes en su criptocomunidad local de Winnipeg. Bogdan está centrado actualmente en el sector de las cadenas de bloques y ofrece asesoramiento, servicios de consultoría y contactos a los expertos en la materia. Bogdan ha cofundado recientemente Bitjob, una plataforma de cadenas de bloques para autónomos dirigida a estudiantes, y ha participado en varios proyectos de ICO, como PayPie.com.',
    'teamAdvisersPartners#132': 'Carrera y proyectos: US Blockchain Association, Tuurnt, Trends Project, BiNeuro, Zichain, Graphen Tech, CyClean, Swachhcoin y CINDX.',
    'teamAdvisersPartners#133': 'Graham Doggart',
    'teamAdvisersPartners#134': '20 años de desarrollo empresarial y cumplimiento de marketing relativo a tecnofinanzas',
    'teamAdvisersPartners#135': 'Ejecución de campañas por valor de 100 000 000 $',
    'teamAdvisersPartners#136': 'Asesor en más de 10 proyectos de cadenas de bloques este año',
    'teamAdvisersPartners#137': 'Soy presidente de un proyecto de criptomonedas oculto además de cofundador y jefe de estrategia de Dynamic Abundance, una asesoría especializada en tecnología de cadenas de bloques y tecnofinanzas, desde 2012.',
