    'roadmap#3': `İlk olarak CINDX platformu için bir temel oluşturuyoruz. Bu aşamada, Minimum Kullanılabilir Ürün geliştiriliyor. MKÜ, takas ve çoğaltma protokolüne erişim sağlayacak. Kaynaklarımızın büyük bir kısmı, platformun ölçeklenebilirliğine ve güvenliğine ayrılmıştır.`,
    'roadmap#4': `Platformun CINDX.alpha sürümü, bir blokzinciri mimarisi ve akıllı sözleşmeler için temel bir yerleşim sistemi içerir. CINDX.market ve CINDX.trade bileşenleri de ek işlevler kazanıp bazı değişikliklerden geçer.`,
    'roadmap#5': `CINDX.alpha sürümündeki blokzinciri mimarisine dayanarak, kullanıcıların iletişim kurmalarını ve faydalı içerikler öğrenip ödül kazanmalarını sağlayan CINDX.talk bileşenini ekliyoruz. Piyasa bilgisini analiz etmek ve bunu üçüncü taraf hizmetlerine sunmak için, platformun CINDX.core bileşenine bir analitik modül eklenecektir.`,
    'roadmap#6': `Bu aşamada, CINDX.market ve CINDX.trade bileşenlerini ekliyoruz. Yöneticiler endeks bazlı ürünler ekleyebilir ve terminal esnek bir risk yönetimi sistemi ile entegre olur.`,
    'roadmap#7': `CINDX.talk, herkese açık ve özel iletişim kanallarına sahip olur. Bu aşamada, içerikler birbirinden ayrılabilir.`,
    'roadmap#8': `Bu aşamada platform büyük oranda güncellenir. Kullanıcılar kripto para birimleri ile fiat para birimlerini depolayıp takas edebilmeye başlar. CINDX.trade, sipariş yürütme ve risk yönetiminin için son derece esnek bir sistem kullanmaya başlar. Pazaryeri devreye alınır ancak bu aşamada sadece CINDX ve iş ortakları tarafından geliştirilen uygulamalara yer verilir.`,
    'roadmap#9': `CINDX.talk son sürümüne jeton bazlı bir puanlama sistemi eklenir. Ayrıca yeni bir ürün türü olan “Algoritmik ticaret” ve takas ile çoklu takas siparişleri arasında otomatik arbitraj işlevi eklenecektir.`,
    'roadmap#10': `İlk olarak CINDX platformu için bir temel oluşturuyoruz. Bu aşamada, Minimum Kullanılabilir Ürün geliştiriliyor. MKÜ, takas ve çoğaltma protokolüne erişim sağlayacak. Kaynaklarımızın büyük bir kısmı, platformun ölçeklenebilirliğine ve güvenliğine ayrılmıştır.`,
    'roadmap#11': `Platformun CINDX.alpha sürümü, bir blokzinciri mimarisi ve akıllı sözleşmeler için temel bir yerleşim sistemi içerir. CINDX.market ve CINDX.trade bileşenleri de ek işlevler kazanıp bazı değişikliklerden geçer.`,
    'roadmap#12': `CINDX.alpha sürümündeki blokzinciri mimarisine dayanarak, kullanıcıların iletişim kurmalarını ve faydalı içerikler öğrenip ödül kazanmalarını sağlayan CINDX.talk bileşenini ekliyoruz. Piyasa bilgisini analiz etmek ve bunu üçüncü taraf hizmetlerine sunmak için, platformun CINDX.core bileşenine bir analitik modül eklenecektir. Yeni bir CINDX.hub bileşeni, CINDX'in sağladığı piyasa verilerini analiz etmek için uygulama geliştirmeyi sağlar.`,
    'roadmap#13': `Bu aşamada, CINDX.market ve CINDX.trade bileşenlerini ekliyoruz. Yöneticiler endeks bazlı ürünler ekleyebilir ve terminal esnek bir risk yönetimi sistemi ile entegre olur. Geliştiriciler için, CINDX üzerinde uygulama geliştirmeyi kolaylaştıracak bir UGK yayımlanır.`,
    'roadmap#14': `CINDX.talk, herkese açık ve özel iletişim kanallarına sahip olur. Bu aşamada, içerik türleri birbirlerinden ayırt edilebilir. CINDX.hub, uygulama geliştirmeyi daha da kolaylaştıran bir Github entegrasyonuna sahip olur.`,
    'roadmap#15': `Bu aşamada platform büyük oranda güncellenir. Kullanıcılar kripto para birimleri ile fiat para birimlerini depolayıp takas edebilmeye başlar. CINDX.trade, sipariş yürütme ve risk yönetiminin için son derece esnek bir sistem kullanmaya başlar. Pazaryeri devreye alınır ancak bu aşamada sadece CINDX ve iş ortakları tarafından geliştirilen uygulamalara yer verilir. CINDX.hub, tarihsel piyasa verilerine dayanan bir test sistemi ile ticaret stratejileri geliştirmek için tam ölçekli bir editöre sahip olur.`,
    'roadmap#16': `CINDX.talk son sürümüne jeton bazlı bir puanlama sistemi eklenir. Ayrıca yeni bir ürün türü olan “Algoritmik ticaret” ve takas ile çoklu takas siparişleri arasında otomatik arbitraj işlevi eklenecektir. Kod denetimi için üst düzey bir platform ve CINDX.talk'da jeton bazlı derecelendirmeye dayalı topluluk uzman değerlendirmesi oluşturulur.`,
    'roadmap#17': `İlk olarak CINDX platformu için bir temel oluşturuyoruz. Bu aşamada, Minimum Kullanılabilir Ürün geliştiriliyor. MKÜ, takas ve çoğaltma protokolüne erişim sağlayacak. Kaynaklarımızın büyük bir kısmı, platformun ölçeklenebilirliğine ve güvenliğine ayrılmıştır.`,
    'roadmap#18': `Platformun CINDX.alpha sürümü, bir blokzinciri mimarisi ve akıllı sözleşmeler için temel bir yerleşim sistemi içerir. CINDX.market ve CINDX.trade bileşenleri de ek işlevler kazanıp bazı değişikliklerden geçer.`,
    'roadmap#19': `CINDX.alpha sürümündeki blokzinciri mimarisine dayanarak, kullanıcıların iletişim kurmalarını ve faydalı içerikler öğrenip ödül kazanmalarını sağlayan CINDX.talk bileşenini ekliyoruz. Piyasa bilgisini analiz etmek ve bunu üçüncü taraf hizmetlerine sunmak için, platformun CINDX.core bileşenine bir analitik modül eklenecektir. Yeni bir CINDX.hub bileşeni, CINDX'in sağladığı piyasa verilerini analiz etmek için uygulama geliştirmeyi sağlar. Ekip, jeton sahiplerine platformun gelirini dağıtacak bir sistem olan CINDX.dao bileşenini geliştirir.`,
    'roadmap#20': `Bu aşamada, CINDX.market ve CINDX.trade bileşenlerini ekliyoruz. Yöneticiler endeks bazlı ürünler ekleyebilir ve terminal esnek bir risk yönetimi sistemi ile entegre olur. Geliştiriciler için, CINDX üzerinde uygulama geliştirmeyi kolaylaştıracak bir UGK yayımlanır.`,
    'roadmap#21': `CINDX.talk, herkese açık ve özel iletişim kanallarına sahip olur. Bu aşamada, içerik türleri birbirlerinden ayırt edilebilir. CINDX.hub, uygulama geliştirmeyi daha da kolaylaştıran bir Github entegrasyonuna sahip olur.`,
    'roadmap#22': `Bu aşamada platform büyük oranda güncellenir. Kullanıcılar kripto para birimleri ile fiat para birimlerini depolayıp takas edebilmeye başlar. CINDX.trade, sipariş yürütme ve risk yönetiminin için son derece esnek bir sistem kullanmaya başlar. Pazaryeri devreye alınır ancak bu aşamada sadece CINDX ve iş ortakları tarafından geliştirilen uygulamalara yer verilir. CINDX.hub, tarihsel piyasa verilerine dayanan bir test sistemi ile ticaret stratejileri geliştirmek için tam ölçekli bir editöre sahip olur. CINDX.dao, jeton sahipleri için bir oy kullanma sistemi elde eder.`,
    'roadmap#23': `CINDX.talk son sürümüne jeton bazlı bir puanlama sistemi eklenir. Ayrıca yeni bir ürün türü olan “Algoritmik ticaret” ve takas ile çoklu takas siparişleri arasında otomatik arbitraj işlevi eklenecektir. Kod denetimi için üst düzey bir platform ve CINDX.talk'da jeton bazlı derecelendirmeye dayalı topluluk uzman değerlendirmesi oluşturulur. CINDX.dao, CINDX’in daha da merkezsiz hale gelmesi için bir delege sistemi sunmaya başlar.`,

