    'tokenSaleDetails#40': `Jeton satışı sırasında, yatırımcılar, gelecekte [CINX] jetonu alma hakkı veren [CINXO] hizmet jetonları satın alırlar.`,
    'tokenSaleDetails#41': `Menkul kıymet jetonu olan [CINX], Emisyon İzni alınmasının ardından piyasaya sürülecektir. (Jeton satışının sona ermesinden sonra Estonya'nın https://www.fi.ee/ adresindeki MDM web sitesinde ve cindx.io adresinde yayınlanacak ve 2019’un ilk çeyreğinden önce gerçekleşecektir.)`,
    'tokenSaleDetails#42': `[CINXO] jetonlarının sahibi; imza teyidi, adres teyidi ve diğer belgelerin temin edilmesi ile MTB prosedürü tamamlandıktan sonra elindeki jetonları 1: 1 oranında menkul kıymet jetonları ile değiştirme hakkına sahiptir ancak bu bir yükümlülük değildir (bkz. CINDX MTB Şeması).`,
    'tokenSaleDetails#43': `[CINXO] jetonları, gelir dağıtımına katılma ve platformda oy kullanma hakkı dışında, menkul kıymet jetonları ile aynı hakları verir.`,

