    'teamAdvisersPartners#59': `Keith Teare`,
    'teamAdvisersPartners#60': `Accelerated Digital Ventures Kurucusu ve Yönetim Kurulu Başkanı`,
    'teamAdvisersPartners#61': `ICOBox, Crypterium, EnergiMine, vb. şirketlerinin danışmanı`,
    'teamAdvisersPartners#62': `TechCrunch'ın kurucu hissedarı`,
    'teamAdvisersPartners#63': `Birleşik Krallık merkezli bir Girişim Şirketi olan Accelerated Digital Ventures firmasında Yönetim Kurulu Başkanı ve Yatırım Komitesi Başkanıyım.`,
    'teamAdvisersPartners#64': `Birleşik Krallık'ta doğdum ve büyüdüm. Daha öncesinde Kaliforniya'nın Palo Alto bölgesinde Archimedes Labs şirketinin kurucusu ve ortağıyım. Ürünler yaratmak, hikâyeler anlatmak ve bunları gerçeğe dönüştürmek için kurucular, ekipler, tasarımcılar ve mühendislerle çalışmaktan keyif alıyorum.`,
    'teamAdvisersPartners#65': `1980'li yıllarından beri sayısız şirketi bizzat kurdum veya kuruluşuna ortaklık ettim. Şirketlerimden ikisi "Unicorn" (tek boynuzlu at) olarak nitelendirildi ve insanlar tarafından 1 milyar doların üzerinde değer biçildi. Hayatım boyunca hep değişimin yaşandığı noktaya odaklandım. 1980'lerdeki ağ kurulumları ve veri tabanları. 1994-1998 arasında İnternet Erişimi. 1998-2010 yılları arasında İnternet Hizmetleri ve İçerik, Mobil Tüketici Uygulamaları ve 2017 yılından bu yana Blok Zinciri.`,
    'teamAdvisersPartners#66': `Kariyer ve projeler: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs ve CINDX`,
