    'teamAdvisersPartners#59': 'Keith Teare',
    'teamAdvisersPartners#60': 'Người sáng lập kiêm Chủ tịch điều hành tại Accelerated Digital Ventures',
    'teamAdvisersPartners#61': 'Cố vấn cho ICOBox, Crypterium, EnergiMine, v.v.',
    'teamAdvisersPartners#62': 'Cổ đông sáng lập của TechCrunch',
    'teamAdvisersPartners#63': 'Tôi là Chủ tịch điều hành kiêm Chủ tịch ủy ban đầu tư tại Accelerated Digital Ventures - Công ty liên doanh của Anh.  Tôi sinh ra và lớn lên ở Anh.',
    'teamAdvisersPartners#64': 'Trước đây tôi là người sáng lập và là thành viên của Archimedes Labs ở Palo Alto, California. Tôi thích làm việc với những nhà sáng lập, các nhóm, nhà thiết kế và kỹ sư để tạo ra sản phẩm, kể chuyện và biến chúng thành hiện thực. Tôi đã thành lập hoặc đồng sáng lập nhiều công ty từ đầu những năm 1980. Một số công ty đã được bán thành công, những công ty khác đã phát hành chứng khoán lần đầu tiên ra công chúng (IPO).',
    'teamAdvisersPartners#65': 'Hai công ty của tôi là Unicorns - được định giá hơn 1 tỷ đô la. Tôi luôn tập trung vào điểm mà thay đổi đang diễn ra. Trong mạng và cơ sở dữ liệu của năm 1980. Trong giai đoạn 1994-1998, Truy cập Internet. Từ 1998-2010 - Dịch vụ Web và nội dung, Ứng dụng người dùng di động và từ năm 2017 là Blockchain.',
    'teamAdvisersPartners#66': 'Sự nghiệp và các dự án: Tech Crunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs and CINDX',
    'teamAdvisersPartners#67': 'Austin Kimm',
    'teamAdvisersPartners#68': 'Giám đốc điều hành dịch vụ tài chính quốc tế',
