    'teamAdvisersPartners#59': 'Keith Teare',
    'teamAdvisersPartners#60': 'Fundador e Presidente Executivo da Accelerated Digital Ventures',
    'teamAdvisersPartners#61': 'Consultor para a ICOBox, Crypterium, EnergiMine, etc.',
    'teamAdvisersPartners#62': 'Fundador e acionista da TechCrunch',
    'teamAdvisersPartners#63': 'Sou o Presidente Executivo e Presidente do Comité de Investimento na Accelerated Digital Ventures - uma empresa de capital de risco no Reino Unido. Sou nascido e criado no Reino Unido.',
    'teamAdvisersPartners#64': 'Anteriormente, fui fundador e sócio na Archimedes Labs em Palo Alto, na Califórnia. Adoro trabalhar com fundadores, equipas, designers e engenheiros para criar produtos, contar histórias e transformá-los em realidade. Fui fundador e cofundador de muitas empresas, no início dos anos 80. Algumas foram vendidas com sucesso, outras sujeitas a OPA.',
    'teamAdvisersPartners#65': 'Duas das minhas empresas eram "unicórnios" - avaliadas por outros em mais de mil milhões $. Sempre me foquei no ponto de mudança. Nos anos 80, redes e bases de dados. No período entre 1994 e 1998, acesso à Internet. Entre 1998 e 2010 foram os Serviços e Conteúdos de Internet, Aplicações de Consumo Móveis. Desde 2017, a área de Blockchain.',
    'teamAdvisersPartners#66': 'Carreira e projetos: TechCrunch, Accelerated Digital Ventures, cScape, EasyNet, Cyberia, RealNames, Venture Network, ICOBox, Crypterium, R/Block, Fluz, EnergiMine, Archimedes Labs e CINDX',
    'teamAdvisersPartners#67': 'Austin Kimm',
    'teamAdvisersPartners#68': 'Diretor Executivo na International Financial Services',
    'teamAdvisersPartners#69': '+30 anos de experiência em serviços financeiros',
    'teamAdvisersPartners#70': 'Criou empresas com um valor de 500 milhões $',
    'teamAdvisersPartners#71': 'Diretor Executivo experiente com resultados provados em serviços financeiros, com enfoque particular na criação de negócios de seguros em mercados em desenvolvimento, onde criei empresas de sucesso com uma avaliação atual superior a 500 milhões $',
    'teamAdvisersPartners#72': ' A experiência mais recente é na Rússia (10 anos), assim como outros cargos na gestão sénior em áreas não afetas aos seguros, incluindo Diretor Executivo da maior plataforma de emprego digital da Rússia, a Workle, e uma grande variedade de cargos de nível C em startups de fintech, incluindo o Crypterium, o primeiro criptobanco digital do mundo.',
    'teamAdvisersPartners#73': 'Carreira e projetos: Aviva, Commercial Union UK, Reinaissance Insurance, Kimm Health & Leisure Ltd, Workle, Crypterium, CryptoIndex, CINDX',
    'teamAdvisersPartners#74': 'Philip Staehelin',
    'teamAdvisersPartners#75': '25 anos de experiência em consultoria em empreendedorismo',
    'teamAdvisersPartners#76': '500 milhões $ em negócios de pagamento alternativos sob liderança',
    'teamAdvisersPartners#77': 'Mentor de +200 startups de 20 países',
    'teamAdvisersPartners#78': 'Sou um inovador e um líder que mistura o otimismo contagiante com o realismo pragmático. Encontro a oportunidade onde niguém a vê.',
    'teamAdvisersPartners#79': 'Consegui alavancar a minha capacidade de "ver as coisas de forma diferente" e "fazer acontecer coisas" em ambientes empresariais, de consultoria e de startups - tudo isto proporciona novas perspetivas alargadas que alimentam novas ideias transformadoras.',
    'teamAdvisersPartners#80': 'Encontrar soluções para desafios complicados é o que me motiva a sair da cama todos os dias.',
    'teamAdvisersPartners#81': 'Carreira e projetos: Roland Berger, Dot Glasses, Transparency International, StartupYard, Bethereum, Gjirafa Inc, Accenture, CINDX',
    'teamAdvisersPartners#82': 'Amarpreet Singh',
    'teamAdvisersPartners#83': 'Consultor Sénior na Global Blockchain Foundation',
    'teamAdvisersPartners#84': 'Experiência na Microsoft, The World Bank e Airbus',
    'teamAdvisersPartners#85': 'Membro de Conselho Consultor de projetos de Blockchain',
    'teamAdvisersPartners#86': 'Entusiasta de Tecnologia/Digital e profissional maduro com anos de experiência no setor da Tecnologia, nas áreas de operações, consultoria e inovação.',
    'teamAdvisersPartners#87': 'É um especialista em Cloud certificado (Microsoft e AWS). Conhecido pela sua versatilidade e flexibilidade. Excelentes capacidades de análise, estratégia e liderança - um jogador de equipa no seu melhor.',
    'teamAdvisersPartners#88': 'Especialidades: Consultoria em Tecnologia, Operações, Computação em Cloud, Fintech, Inovação, ERP, Machine Learning, Gestão de Contas, Desenvolvimento de Parcerias, Desenvolvimento de Negócios e Estratégia.',
    'teamAdvisersPartners#89': 'Amarpreet Singh possui um trabalho muito diversificado, assim como uma vasta formação académica. Possui três Mestrados e viveu/trabalhou/estudou na Índia, Singapura, França, China, África do Sul, Coreia e Canadá.',
    'teamAdvisersPartners#90': 'Carreira e projetos: Microsoft, The World Bank, Airbus, TokenAsia Platform, GOeurika, CINDX',
    'teamAdvisersPartners#91': 'Tyler Sanford',
    'teamAdvisersPartners#92': '8 anos em Marketing Digital na Yelp and Zenefits',
    'teamAdvisersPartners#93': '12 ICO, mais de 225 milhões $ angariados',
    'teamAdvisersPartners#94': 'Utilizador pioneiro de Blockchain',
    'teamAdvisersPartners#95': 'Crescer como atleta deu-me capacidades que consegui aplicar às minhas carreiras e vida pessoal.',
    'teamAdvisersPartners#96': 'Tive a felicidade de ver os resultados que advêm daquele esforço suplementar, enquanto aprendo a lidar com os altos e baixos que acompanham todas as carreiras pessoais.',
    'teamAdvisersPartners#97': 'Sou um indivíduo com uma grande auto-motivação, sempre à procura de continuar o meu crescimento de conhecimentos e talentos, em todos os aspetos dos negócios.',
    'teamAdvisersPartners#98': 'Sempre pronto para um desafio, a minha motivação tem origem na superação de obstáculos com vista a alcançar os meus objetivos.',
    'teamAdvisersPartners#99': 'Carreira e projetos: IPG Holdings, DataBlockChain, TraXion by Puma Technologies, ELYSIAN, Etherinc, FTEC Foundation, CINDX',
