    'teamAdvisersPartners#105': 'Consegui alavancar a minha capacidade de "ver as coisas de forma diferente" e "fazer acontecer coisas" em ambientes empresariais, de consultoria e de startups - tudo isto proporciona novas perspetivas alargadas que alimentam novas ideias transformadoras.',
