    'teamAdvisersPartners#124': 'Carreira e projetos: IPG Holdings, DataBlockChain, TraXion by Puma Technologies, ELYSIAN, Etherinc, FTEC Foundation, CINDX',
    'teamAdvisersPartners#125': 'Bogdan Fiedur',
    'teamAdvisersPartners#126': 'Especialista, investidor e empreendedor em Blockchain e criptomoedas',
    'teamAdvisersPartners#127': 'Cofundador da bitJob e Presidente da Blockchainexperts.io',
    'teamAdvisersPartners#128': 'Consultor de ICO, Especialista da ICOBench',
    'teamAdvisersPartners#129': 'Bogdan Fiedur é um criador de contratos inteligentes, criptoinvestidor, empreendedor e Presidente da http://blockchainexperts.io. Ele tem mais de 20 anos de experiência de TI na construção de websites de comércio eletrónico e está envolvido no desenvolvimento de Blockchain desde há 3 anos.',
    'teamAdvisersPartners#130': 'Bogdan é também um consultor de ICO, que trabalhou com 20 ICO de sucesso até à data, e está profundamente envolvido na oferta de apresentações e formações sobre Blockchain e programação de contratos inteligentes na sua criptocomunidade local de Winnipeg.',
