    'teamAdvisersPartners#59': 'كيث تياري ',
    'teamAdvisersPartners#60': 'مؤسس شركة Accelerated Digital Ventures ورئيس مجلس إدارتها التنفيذي',
    'teamAdvisersPartners#61': 'مستشار لدى ICOBox وCrypterium وEnergiMine وما إلى ذلك.',
    'teamAdvisersPartners#62': 'مساهم مؤسّس في TechCrunch',
    'teamAdvisersPartners#63': 'أنا الرئيس التنفيذي لمجلس الإدارة ورئيس لجنة الاستثمار في شركة Accelerated Digital Ventures، وهي إحدى الشركات الاستثمارية في المملكة المتحدة. وُلِدتُ ونشأت في المملكة المتحدة. ',
    'teamAdvisersPartners#64': 'وأسّست في السابق شركة Archimedes Labs في بالو ألتو بكاليفورنيا وكنت شريكًا فيها. أحب العمل مع المؤسّسين والفِرَق والمصمّمين والمهندسين لإنشاء منتجات مثيرة',
    'teamAdvisersPartners#65': 'ورواية القصص الشيّقة وتحويلها إلى واقع. نجحت في تأسيس الكثير من الشركات وشاركت في تأسيس بعض منها منذ بداية ثمانينيات القرن العشرين. تم بيع بعض هذه الشركات بنجاح، بينما طرح بعضها الآخر أسهمه للاكتتاب العام. بالإضافة إلى ذلك، تم تقدير قيمة اثنتين من شركاتي بما يزيد عن مليار دولار. لطالما انصب تركيزي على اتجاهات حدوث التغيير. ففي ثمانينيات القرن العشرين، قادت الشبكات وقواعد البيانات دفة التغيير. وكان الاتصال بالإنترنت هو موضوع الساعة خلال الفترة بين 1994 و1998. واحتلّت خدمات الويب والمحتوى وتطبيقات المستهلكين على الأجهزة الجوّالة مكانة مهمة خلال الفترة بين 1998 و2010. ويتجه التركيز على تقنية البلوك تشين منذ عام 2017 حتى الآن.',
    'teamAdvisersPartners#66': 'االحياة المهنية والمشروعات: Tech Crunch وAccelerated Digital Ventures وcScape وEasyNet وCyberia وRealNames وVenture Network وICOBox وCrypterium وR/Block وFluz وEnergiMine وArchimedes Labs وCINDX',
