    'teamAdvisersPartners#71': 'أنا رئيس تنفيذي يتمتع بخبرة عالية مُثبَتة بالنتائج في مجال توفير الخدمات المالية، مع التركيز بشكلٍ خاص على تأسيس شركات التأمين في الأسواق النامية، حيث نجحت في بناء شركات تزيد قيمتها حاليًا عن 500 مليون دولار. عملتُ في الآونة الأخيرة في روسيا (10 سنوات)، كما أني شغلت أدوار أخرى في قطاعات مختلفة بخلاف التأمين ككبير مديرين، بما في ذلك الرئيس التنفيذي لمنصة Workle، وهي أكبر منصة توظيف لمبيعات الإنترنت في روسيا، وعملتُ أيضًا في مناصب إدارية كُبرى ومتنوّعة في شركات ناشئة تعمل في مجال التقنيات المالية، مثل Crypterium، الذي يُعد أول بنك رقمي للعملات المشفّرة في العالم. الحياة المهنية والمشروعات: Aviva وCommercial Union UK وReinaissance Insurance وKimm Health & Leisure Ltd وWorkle وCrypterium وCryptoIndex وCINDX',
