    'teamAdvisersPartners#78': 'أنا قائد شغوف بالابتكار ويرتكز عملي على الجمع بين التفاؤل والمنهج الواقعي العقلاني. وأحاول دائمًا العثور على الفرص في المناطق التي لا يرى أحدٌ أي أمل فيها. بعبارة أخرى، نجحت في مناسبات عديدة في استغلال قدراتي "لرؤية الأمور بشكلٍ مختلف" و"تحقيق الأهداف المنشودة" في بيئات المؤسسات ومجال تقديم الاستشارات والشركات الناشئة، مما أدّى بالتالي إلى مساعدتي على العمل بزوايا جديدة وتشجيعي على تطبيق أفكار لم يتطرّق إليها أحد من قبل. يستند مصدر إلهامي إلى العثور على الحلول اللازمة للتصدّي للتحديات المختلفة. الحياة المهنية والمشروعات: Roland Berger وDot Glasses وTransparency International وStartupYard وBethereum وGjirafa Inc وAccenture وCINDX',
