    'teamAdvisersPartners#128': 'مستشار في مجال الطرح الأولي للعملات وخبير لدى ICOBench',
    'teamAdvisersPartners#129': 'يُعد بوغدان فيدور واحدًا من المطوّرين المميزين لميزة العقود الذكية، كما أنه مستثمر شغوف بالعملات المشفّرة ورائد أعمال ورئيس منصة http://blockchainexperts.io. يتمتع بوغدان بخبرة تزيد عن 20 عامًا في مجال تقنية المعلومات، وتحديدًا في إنشاء مواقع التجارة الإلكترونية، وقد شارك في تطوير أدوات البلوك تشين خلال السنوات الثلاث الماضية. بالإضافة إلى ذلك، فإنه يعمل كمستشار في مجال الطرح الأولي للعملات ونجح في إطلاق 20 حدثًا من أحداث الطرح الأولي للعملات حتى الآن، كما أنه يقدّم عروضًا تقديمية ووِرَش عمل حول تقنية البلوك تشين وبرمجة العقود الذكية في نطاق مجتمعه المحلي المعني بالعملات المشفّرة في وينيبيغ. يركز بوغدان الآن على مجال البلوك تشين ويقدم خدماته الاستشارية لخبراء البلوك تشين ويفيدهم بمعارفه. أسّس بوغدان مؤخرًا منصة Bitjob، وهي منصة مستقلة قائمة على تقنية البلوك تشين مخصّصة للطلاب، وشارك أيضًا في عدد من مشروعات الطرح الأولي للعملات كعضو في الفِرَق المنفّذة لهذه المشروعات، مثل PayPie.com. الحياة المهنية والمشروعات: US Blockchain Association وTuurnt وTrends Project وBiNeuro وZichain وGraphen Tech وCyClean وSwachhcoin وCINDX',
