    'teamAdvisersPartners#137': 'أعمل كرئيس مجلس إدارة مشروع Stealth Cryptocurrency Project، كما أني شاركت في تأسيس Dynamic Abundance، وهي إحدى الشركات الاستشارية العاملة في مجال البلوك تشين والتقنيات المالية منذ عام 2012، وأشغل أيضًا منصب المسؤول عن وضع الإستراتيجيات فيها. يتمثّل تخصصي في إنشاء وإنهاء تنفيذ عروض العملات الرمزية، كما أني عملت على عدد كبير من مشروعات البلوك تشين والعملات المشفّرة والطرح الأولي للعملات على الصعيد الدولي. وعلى مدار ما يزيد عن 20 عامًا، نجحت في تقديم استشارات في مجالات إدارة التقنيات والامتثال القانوني والمالي وعلاقات المستثمرين، بالإضافة إلى وضع إستراتيجيات النمو وتصميم وتأليف عدد كبير من المواد. الحياة المهنية والمشروعات: Stealth Cryptocurrency Project وDynamic Abundance وELYSIAN وFox Trading FX وProfede وWorltdopoly وCINDX',
