    'teamAdvisersPartners#144': 'مستشارة الطرح الأولي للعملات لثمانية مشروعات',
    'teamAdvisersPartners#145': 'نجحت في تأسيس إحدى الشركات الاستشارية العاملة في مجال تقنيات البلوك تشين، وتُعرَف باسم Dynamic Abundance. أفضّل المنهج "العملي" ويستند تخصّصي في الأساس إلى تحليل عمليات الطرح الأولي للعملات وجميع الجوانب المتعلّقة بها وإدارة المشروعات. ونظرًا لأن مهاراتي تعتمد أساسًا على إدارة الشركات وتنميتها، فيمكنني القول إن عملي يتلخّص في إدارة أي مشروع بدءًا من مرحلة وضعه وحتى تنفيذه. وبوجهٍ عام، أحب حل المشكلات وأتعامل مع أي مشروع أعمل به وكأنه مشروعي الأول والأخير. فأنا أسعى للفوز دائمًا! على مدار ما يزيد عن 20 عامًا من الخبرة في مجال الإدارة، نجحت في تكوين شبكة قوية من العلاقات. وحاليًا، أعمل في مجال تقديم الاستشارات المالية والتقنية والتسويقية وتلك المتعلّقة بالمبيعات وضمان الجودة لصالح المشروعات رفيعة المستوى القائمة على تقنية البلوك تشين. الحياة المهنية والمشروعات: Dynamic Abundance وELYSIAN وWorldopoly وConfidential ICO وFox Trading FX وProfede وMarket Digital وCINDX',
