    'teamAdvisersPartners#159': 'مؤسس العديد من الشركات في قطاعي الأمان والشؤون المالية.',
    'teamAdvisersPartners#160': 'يُعد موتي أحد القادة المتميزين في مجال العلاقات العامة لقطاعات مثل تقنية البلوك تشين والعملات المشفّرة والشركات الناشئة والعملات الافتراضية.',
    'teamAdvisersPartners#161': 'أسّس موتي العديد من الشركات في قطاعي الأمان والشؤون المالية. ويتمتع بخبرة قِوامها أعوام طويلة من العمل كمدير لصناديق التحوّط وصناديق استثمار الأموال، كما أنه حاصل على درجة الماجيستير في إدارة الأعمال وتحديدًا في قطاع الشؤون المالية والتسويق من جامعة مانشيستر ودرجة البكالوريوس في إدارة الأعمال من جامعة فيرلاي ديكينسون. شارك موتي كرئيس تنفيذي في تأسيس وكالة Blonde 2.0 العالمية للعلاقات العامة، وهي إحدى وكالات العلاقات العامة الحائزة على جوائز، والتي لقّبتها Forbes بإحدى الوكالات الناشئة "ذات النفوذ القوي" على المستوى القومي. وباعتبار موتي رئيسًا تنفيذيًا مشاركًا لوكالة Blonde 2.0، فهو مسؤول عن التعامل مع جوانب العلاقات العامة، بالإضافة إلى جانبي وضع الإستراتيجيات وإدارة الأزمات. تضّم Blonde 2.0 فريقًا مؤلَّفًا من 35 عضوًا محترفًا، وتُعد واحدة من الوكالات المتميزة في مجال العلاقات العامة لقطاعات مثل تقنية البلوك تشين والعملات المشفّرة والشركات الناشئة والعملات الافتراضية.',
