    'teamAdvisersPartners#167': 'نجح في جمع أكثر من 18 مليون دولار أمريكي لشركته الناشئة عندما كان طالبًا في كلية الحقوق',
    'teamAdvisersPartners#168': 'وُلِد ليونارد غرايفير في الاتحاد السوفيتي وانتقل إلى الولايات المتحدة مع والديه عندما كان طفلاً صغيرًا. أنشأ ليونارد أثناء دراسته في كلية الحقوق شركة ناشئة ونجح في جمع ما يزيد عن 18 مليون دولار أمريكي لتمويلها. لم تستمر هذه الشركة، ولكن منذ ذلك الحين، ساعد ليونارد أكثر من 400 شركة ناشئة. بوجهٍ عام، يتمتع ليونارد بما يزيد عن 10 أعوام من الخبرة في مجال التمويل بالعملات الافتراضية ووضع هياكل الصناديق الاستثمارية وإجراءات بذل العناية الواجبة (أي ما يمثّل كلاً من الصناديق الاستثمارية والشراكات المحدودة). ينصح ليونارد جميع رواد الأعمال بالسعي وراء النجاح فيما يثير اهتماماتهم بدلاً من الاكتفاء بالسير وراء الاتجاهات السائدة.نجح ليونارد في تكوين شبكة من العلاقات المهنية القوية مع أصحاب العديد من الصناديق الاستثمارية الدولية.',
